} */

.host {
  background: black;
  color: var(--paper-yellow-a700);
  /* border-right: 1px solid black; */
}

.host:hover {
  background: black;
  color: var(--paper-yellow-a700);
  /* border-right: 1px solid black; */
}

.join {
  background: var(--paper-yellow-a700);
  color: black;
  /* border-left: 1px solid black; */
}

.join:hover {
  background: var(--paper-yellow-a700);
  color: black;
  /* border-left: 1px solid black; */
}

.emoji-mart {
  position: absolute;
  top: 23%;
}
.emoji-mart-scroll {
  border-bottom: 0;
  margin-bottom: 6px;
}

.emoji-mart-scroll + .emoji-mart-bar {
  display: none;
}

.emoji {
  font-weight: 400;
  font-family: apple color emoji, segoe ui emoji, noto color emoji,
    android emoji, emojisymbols, emojione mozilla, twemoji mozilla,
    segoe ui symbol;
  font-size: 1.2em;
}

.emoji-button {
  border: none;
}

.emoji-button:hover {
  border: none;
}

.chat_box {
  height: 57vh;
  overflow-y: auto;
}

.chat_user_name {
  /* font-size: 0.7em; */
  margin: 0 17px 2px 0;
  color: grey;
}

.self {
  float: right;
}

.player_container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.left_tile_custom {
  flex-direction: column;
  justify-content: center;
}

.avatar {
  width: 30px;
  text-align: center;
  height: 30px;
  background-color: green;
  border-radius: 50%;
  font-size: 1.2em;
  color: white;
  text-transform: uppercase;
  margin: 5px;
  display: inline-block;
}

.avatar_container {
  /* display: flex; */
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
}

.info_box {
  margin-top: 2%;
  /* margin-right: 2%; */
}

.changeVideo_divider {
  margin: 0.2rem 0 0.8rem 0;
}

.homepage_left_hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.homepage_left_hero h1 {
  font-size: 3em;
}

.homepage_explainer {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  /* transition: width 0.5s; */
  /* border: 2px solid black; */
}

.laptop_svg {
  height: 100px;
  margin: 12px;
}

