.sync_svg {
  height: 100px;

  animation-name: ckw;
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  transform-origin: 50% 50%;
  display: inline-block;
}
@keyframes ckw {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.hero_video {
  border: 2px solid black;
  border-radius: 5px;
}

.full_height {
  min-height: 100vh;
}

.full_height .tile {
  justify-content: center;
}

.grid_row {
  min-height: 100vh;
  display: grid;
  /* grid-template-columns: repeat(auto-fit, 1fr); */
  align-items: center;
  grid-auto-rows: 1fr;
  align-items: stretch;
}

.grid_row .content_party {
  margin: 0px !important;
  padding: 0px 40px 40px 0px;
  /* border-right: 1px solid black;
  border-bottom: 1px solid black; */
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  background-color: var(--paper-grey-300);
}

.grid_row .content_copy {
  margin: 0px !important;
  padding: 0px 40px 40px 0px;
  /* border-left: 1px solid black;
  border-bottom: 1px solid black; */
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  background-color: var(--paper-blue-700);
}

.grid_row .content_sync {
  margin: 0px !important;
  padding: 0px 40px 40px 0px;
  /* border-right: 1px solid black;
  border-top: 1px solid black; */
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  background-color: var(--paper-yellow-a700);
}

.grid_row .content_chat {
  margin: 0px !important;
  padding: 0px 40px 40px 0px;
  /* border-left: 1px solid black;
  border-top: 1px solid black; */
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  background-color: var(--paper-red-a400);
}

.expliner_img {
  display: block;
  width: 100%;
  box-shadow: rgba(84, 70, 35, 0.15) 0px 2px 8px,
    rgba(84, 70, 35, 0.15) 0px 1px 3px;
  border-radius: 4px;
  margin-bottom: 18px;
  width: 450px;
  height: 200px;
}

.floating {
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  margin-left: 30px;
  margin-top: 5px;
  height: 100px;
  margin: 5px;
}

@keyframes floating {
  from {
    transform: translate(0, 0px);
  }
  65% {
    transform: translate(0, 15px);
  }
  to {
    transform: translate(0, -0px);
  }
}

@media all and (min-width: 700px) {
  .grid_row {
    grid-template-columns: 1fr 1fr;
  }
}

.grid_row h4 {
  color: black;
  margin-top: 0.8em;
  user-select: none;
  font-weight: bold;
}

.not-selectable {
  user-select: none;
}

.navbar-item img {
  max-height: 2.5rem;
  max-width: 3.5rem;
  cursor: pointer;
}

.navbar-item a {
  max-height: 2.5rem;
  color: white;
  cursor: pointer;
  text-decoration: none;
}

img {
  height: 100%;
  max-width: 100%;
}

.navbar-item {
